// ->->

let SubModule = (name, path, subType) => ({
    name, path, subType
})

// 
let subModuleGroup1 = [
    SubModule('', 'all', 0),
    SubModule('', 'infomation', 1),
    SubModule('', 'biology', 3),
    SubModule('', 'material', 4),
    SubModule('', 'energy', 5),
    SubModule('', 'laser', 6),
    SubModule('', 'auto', 7),
    SubModule('', 'space', 8),
    SubModule('', 'ocean', 9),
    SubModule('', 'other', 10),
]

let Category = (name, path, meta, modules) => ({
    name, path, meta, modules
})

let Module = (name, path, type, containSubModule = false) => ({
    name, path, type,
    subModules: containSubModule ? subModuleGroup1 : undefined
})


export default [
    Category('', 'overview', {
        title:'OVERVIEW'
    },[
        Module('', 'introduction', 0),
        Module('', 'staff', 1),
    ]),
    Category('', 'info', {
        title:'NEWS'
    },[
        Module('', 'news', 2),
        Module('', 'notice', 3),
    ]),
    Category('', 'achievement', {
        title:'ACHIEVEMENT'
    },[
        Module('', 'technology', 4, true),
        Module('', 'patent', 5),
        Module('', 'case', 6),
    ]),
    Category('', 'experts', {
        title:'EXPERTS'
    },[
        Module('', 'experts',7,true)
    ]),
    Category('', 'translation', {
        title:'TRANSLATION'
    },[
        Module('', 'translation',8,true)
    ]),
    Category('', 'guidence',{
        title:'GUIDENCE'
    },[
        Module('', 'guidence',9)
    ]),
    Category('', 'download',{
        title:'DOWNLOADS'
    },[
        Module('', 'download',10)
    ]),
]








